<template>
    <div>
      <el-header class="work-header">
        <el-row type="flex" justify="space-around">
        <el-col :span="6"><div><img src="./images/Anyview.svg" alt="Anyview可视化编程平台" class="header-logo"></div></el-col>
        <el-col :span="6"><div><el-input placeholder="请输入内容"  prefix-icon="el-icon-search"></el-input></div></el-col>
        <el-col :span="6">
          <div>
            <el-dropdown trigger="hover">
              <span>
                <el-avatar  class="header-avatar"  
                            shape="square" 
                            :size="45" 
                            fit="fill" 
                            src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg">
                </el-avatar>
              <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command='personal_center'><i class="el-icon-menu"></i>个人中心</el-dropdown-item>
              <el-dropdown-item><i class="el-icon-bell"></i>我的消息</el-dropdown-item>
              <el-dropdown-item><i class="el-icon-star-off"></i>我的收藏</el-dropdown-item>
              <el-dropdown-item><i class="el-icon-tickets"></i>我的笔记</el-dropdown-item>
              <el-dropdown-item><i class="el-icon-time"></i>我的进度</el-dropdown-item>
              <el-dropdown-item command="logout"><i class="el-icon-warning"></i>退出账号</el-dropdown-item>
              </el-dropdown-menu>
              </span>
            </el-dropdown>
            <span class="user-name"><i>{{userName}}</i></span>
          </div>
        </el-col>
      </el-row>
      </el-header>
      <el-main class="work-main">
        <!-- <work-card></work-card> -->
      </el-main>
      <el-footer>
        <span>©</span>
        <span>广东工业大学</span>
        <span>可视化创新与系统工具研究工作室</span>
        <span><a href="">Anyview团队</a></span>
      </el-footer>
    </div>
</template>
<script>
 import workCard from 'components/courseCard.vue';
 import {getQuestionList} from '@/api/work'
 import {setCache,getCache} from 'static/cache.js';
//  import canvas from '../../../static/canvas.js';
export default {
    mounted () {
      this._getQuestionList()
      //  canvas(30)   //执行canvas动画
    },
    data () {
      return {
      }
    },
    methods: {
      _getQuestionList() {
        getQuestionList(this.userId).then((e)=>{
          console.log(e);
        })
      }
    },
    computed: {
      userId(){
        return JSON.parse(getCache("user")).id
      },
      userName() {
        return JSON.parse(getCache("user")).name
      }
    },
    components: {
     workCard
    }
}
</script>

<style>
  @import "../../assets/css/base.css";
</style>
<style lang="scss" scoped>
   .work-header{
     height: 20%!important;   //组件库由于有默认样式，加important覆盖
     margin-top: 30px;
     .header-logo{
       margin-top: -100px;
       height: 250px;
       width: 250px;
     }
     div{
       text-align: center;
     }
     .header-avatar{
       cursor: pointer;
     }
     .user-name{
      color: rgb(144, 164, 174);
      cursor: pointer;
      padding: 5px;
      font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
     }
   }
    .el-dropdown-menu{
      width: 200px!important; 
    }

   .work-main{
     height: 76%;
   }
</style>


