<template>
    <div id="app">
        <router-view id='router'></router-view>
    </div>
</template>

<script>
export default {
     watch: {
      // 检测哪个路由过来
      '$route' (to, from){
        if(from.name=="login"&&to.name=="work") {
          this.$message({
            showClose: true,
            message: '欢迎您,'+JSON.parse(localStorage.getItem("user")).name,
            type: 'success'
          });
        }
         console.log(from.name,to.name)
      }
    },
}
</script>

<style>
#app {
    height: 100%;
}
#router {
    height: 100%;
}
</style>



